const gatewayShiftX = 210
// 
const startPointX = 40
const startPointY = 50
// 
const shiftX = 152
const toolShiftX = 14
const shiftY = 110
// 
const deviationY = 15
// 
const toolsWidth = 60
const tabContentWidth = 414
const maxShiftX = 100
//  -  - 
const maxWidth = Math.max(document.body.clientWidth - toolsWidth - tabContentWidth - maxShiftX, 1200)
// 
let lastNodeX = 60
let lastNodeY = 50
// 
let gatewayShiftY = 0
// 
const gatewayShiftYList = []
//  id -> id
let gatewayGroup = {}
// 
const gatewayStack = []
// 
let gatewayCycleList = {}
// 
let lastPoint = null
// 
let endPoint = null
// Id
let nowId = null
// ,nowId
let group = {}
// 
let newLocations = {}
// 
const gatewayFlagList = []
// 
let isStartPoint = false
// 
let lastPointType = 'startpoint'
// 
let gatewayCycleExecute = {}
// 
let lines = null
// 
let locations = []
// 
const convergeGatewayNodeXList = []
// x
const lastNodeXList = []
// Y
const resetGatewayList = []
// 
let LastLocations = null
// line
const overBorderLine = []
import tools from './tools.js'

const formatPositionUtils = {
    formatPosition (oldLines, oldLocations) {
        LastLocations = oldLocations
        lines = oldLines
        //  
        this.initData()
        // 
        for (let line of lines) {
            let sourceId = line.source.id
            let targetId = line.target.id
            if (group[sourceId]) {
                group[sourceId].push(targetId)
            } else {
                group[sourceId] = [targetId]
            }
        }
        // 
        for (let location of oldLocations) {
            let type = location['type']
            if (type === 'startpoint') {
                // 
                lastPoint = location
                nowId = location.id
            }
            else if (type === 'endpoint') {
                // 
                endPoint = location
            }
            else {
                let locationId = location.id
                // 
                newLocations[locationId] = location
                // 
                if (type === 'parallelgateway' || type === 'branchgateway') {
                    gatewayCycleList[locationId] = {}
                    gatewayCycleExecute[locationId] = {}
                    // 
                    let [gatewayRecursionList] = this.recursionGateway(gatewayStack, locationId, oldLocations)
                    for (let x in gatewayRecursionList) {
                        for (let y in gatewayRecursionList[x]) {
                            let nodeId = gatewayRecursionList[x][y]
                            gatewayCycleList[locationId][nodeId] = oldLocations.find(location => location.id === nodeId)
                        }
                        // 
                        // 
                        gatewayCycleExecute[locationId][x] =  gatewayRecursionList[x].length - 1
                    }
                }
            }
        }
        // locations
        for (let x in gatewayCycleList){
            for (let y in gatewayCycleList[x]) {
                for (let z in gatewayCycleList[x][y]){
                    let id = gatewayCycleList[x][y][z]
                    if (newLocations[id] === undefined) {
                        continue
                    }
                    delete newLocations[id]
                }
            }
        }
        // 
        locations = []
        // 
        let locationJson = tools.deepClone(lastPoint)
        // 
        locationJson['x'] = startPointX
        locationJson['y'] = startPointY
        locations.push(locationJson)
        // 
        this.recursionData(newLocations)
        // 
        this.isOverBorder()
        // 
        this.computeAndDrawNode(endPoint)
        return {'lines': lines, 'locations': locations, 'overBorderLine': overBorderLine}
    },
    /**
     *
     * @param {*} gatewayStack Id
     * @param {*} locationId Id
     * @param {*} oldLocations locations
     */
    recursionGateway (gatewayStack, locationId, oldLocations) {
        // 
        let gatewayRecursionList = []
        // 
        let groupLocationList = group[locationId]
        // 
        let recursionList = []
        // id
        let findId = null
        for (let locationIndex in groupLocationList) {
            gatewayStack.push(locationId)
            // 
            findId = groupLocationList[locationIndex]
            while (true) {
                // 
                let findLocation = oldLocations.find(location => location.id === findId)
                let findType = findLocation['type']
                if (findType === 'convergegateway') {
                    //  
                    if (locationIndex + 1 !== groupLocationList.length) {
                        gatewayGroup[gatewayStack.pop()] = findId
                        recursionList.push(findId)
                        break
                    }
                }
                else if (findType === 'parallelgateway' || findType === 'branchgateway') {
                    recursionList.push(findId)
                    //  
                    let result = this.recursionGateway(gatewayStack, findId, oldLocations)
                    // 
                    findId = result[1]
                }
                else {
                    recursionList.push(findId)
                }
                // 
                findId = group[findId][0]
            }
            // 
            gatewayRecursionList.push(recursionList)
            recursionList = []
        }
        return [gatewayRecursionList, findId]
    },
    /**
     *
     * @param {*} newLocations 
     * @param {*} gatewayNodeYList Y
     * @param {*} newLastNodeX x
     * @param {*} newLastNodeY y
     */
    recursionData (newLocations, gatewayNodeYList = [], newLastNodeX = null , newLastNodeY = null) {
        // 
        for (let i in newLocations) {
            // 
            let lastList = group[nowId]
            // 
            // undefined
            if (lastList === undefined || newLocations[lastList[0]] === undefined) {
                break
            }
            // Id
            let lastId = nowId
            if (newLastNodeX === null) {
                newLastNodeX = lastNodeX
            }
            if (newLastNodeY === null) {
                newLastNodeY = lastNodeY
            }
            let executeLength = lastList.length
            for (let executeNodeIndex = 0; executeNodeIndex < executeLength; executeNodeIndex++) {
                nowId = lastList[executeNodeIndex]
                let location = newLocations[nowId]
                // ,
                if (location === undefined) {
                    break
                }
                // 
                if (gatewayCycleExecute[lastId] === undefined) {
                    // 
                    this.isOverBorder()
                    let result = this.computeAndDrawNode(location, gatewayNodeYList, executeLength)
                    if (result) {
                        gatewayNodeYList = result[2]
                    }
                } else {
                    // 
                    let gatewayCycleExecuteLength = gatewayCycleExecute[lastId][executeNodeIndex]
                    let copynewLastNodeX = newLastNodeX
                    let copynewLastNodeY = newLastNodeY
                    for (let gatewayIndex = 0; gatewayIndex < gatewayCycleExecuteLength; gatewayIndex++) {
                        let result = this.computeAndDrawNode(location, gatewayNodeYList, executeLength, executeNodeIndex, copynewLastNodeX, copynewLastNodeY, gatewayIndex, gatewayCycleExecuteLength)
                        copynewLastNodeX = result[0]
                        if (result[1] !== null) {
                            // Y
                            copynewLastNodeY = result[1]
                        }
                        gatewayNodeYList = result[2]
                        nowId = group[nowId]
                        // 
                        location = newLocations[nowId]
                    }
                    
                    // 
                    lastNodeXList.push(copynewLastNodeX)
                }
                if (executeNodeIndex + 1 === executeLength && executeNodeIndex > 0) {
                    // 
                    let location = newLocations[gatewayGroup[lastId]]
                    if (convergeGatewayNodeXList.length >= 1) {
                        lastNodeXList.push(convergeGatewayNodeXList.pop())
                    }
                    
                    // X
                    let maxNodeX = Math.max(...lastNodeXList)
                    let [resultNodeX, resultNodeY, resultGatewayNodeYList] = this.computeAndDrawNode(location, gatewayNodeYList, executeLength, 0, maxNodeX, newLastNodeY)
                    convergeGatewayNodeXList.push(maxNodeX)
                    gatewayNodeYList = resultGatewayNodeYList
                    lastNodeX = resultNodeX
                    // 
                    gatewayFlagList.pop()
                    nowId = location.id
                    gatewayShiftYList.splice(0, gatewayShiftYList.length)
                    break
                }
            }
        }
        return gatewayNodeYList
    },
    /**
     *
     * @param {*} location 
     * @param {*} gatewayNodeYList 
     * @param {*} executeLength 
     * @param {*} executeNodeIndex index
     * @param {*} newLastNodeX X
     * @param {*} newLastNodeY Y
     * @param {*} gatewayIndex inex
     * @param {*} gatewayCycleExecuteLength 
     */
    computeAndDrawNode (location, gatewayNodeYList = [], executeLength = null, executeNodeIndex = 0, newLastNodeX = null , newLastNodeY = null, gatewayIndex = 0, gatewayCycleExecuteLength = 0) {
        if (location === undefined) {
            // 
            return
        }
        // locationx,y
        let locationJson = tools.deepClone(location)
        let type = location['type']
        if (type === 'tasknode' || type === 'subflow') {
            // 
            let gatewayFlag = null
            // 
            if (gatewayFlagList.length - 1 >= 0) {
                gatewayFlag = gatewayFlagList[gatewayFlagList.length - 1]
            } else {
                gatewayFlag = false
            }
            // 
            if (gatewayFlag) {
                let tempLastNodeY = Math.max(...gatewayNodeYList)
                if (newLastNodeY < tempLastNodeY || (executeNodeIndex === 0 && gatewayIndex === 0)) {
                    newLastNodeY = tempLastNodeY
                }
                let nodeY = null
                let perviousPoint = this.getPreviousPoint(location.id)
                if (executeNodeIndex === 0) {
                    nodeY = newLastNodeY - deviationY
                } else if (perviousPoint.type === 'tasknode' || perviousPoint.type === 'subflow' ) {
                    nodeY = newLastNodeY - deviationY
                } else if (gatewayIndex > 0 ){
                    nodeY = newLastNodeY + (executeNodeIndex - 1) * shiftY - deviationY
                } else {
                    nodeY = newLastNodeY + shiftY - deviationY
                }
                newLastNodeX += shiftX * 1.2
                locationJson['x'] = newLastNodeX
                locationJson['y'] = nodeY
                locations.push(locationJson)
                gatewayShiftYList.push(nodeY + deviationY)
                gatewayNodeYList.push(nodeY + deviationY)

                let sourceLine = lines.find(line => line.target.id === location.id)
                let targetLine = lines.find(line => line.source.id === location.id)
                if (executeNodeIndex === 0) {
                    // 
                    sourceLine.source.arrow = 'Right'
                    targetLine.target.arrow = 'Left'
                } else {
                    // 
                    if (gatewayIndex === 0) {
                        // NN>2
                        sourceLine.source.arrow = 'Bottom'
                        if ( gatewayCycleExecuteLength === 1) {
                            //  
                            targetLine.target.arrow = 'Bottom'
                        } else {
                            // 
                            targetLine.target.arrow = 'Left'
                        }
                    } else {
                        // 
                        sourceLine.source.arrow = 'Right'
                        targetLine.target.arrow = 'Bottom'
                    }
                }
                if (executeNodeIndex + 1 === executeLength) {
                    // 
                    lastNodeX += shiftX / 1.2
                    if (gatewayShiftY < nodeY) {
                        gatewayShiftY = nodeY
                    }
                }
                lastPointType = type
                return [newLastNodeX, null, gatewayNodeYList]
            } else {
                // 
                // tasknodesubflow
                if (lastPointType === 'tasknode' || lastPointType === 'subflow') {
                    lastNodeX += toolShiftX * 6
                }
                // 
                lines.find(line => line.target.id === location.id).source.arrow = 'Right'
                lines.find(line => line.source.id === location.id).target.arrow = 'Left'

                if (!isStartPoint) {
                    lastNodeX += shiftX / 1.5
                } else {
                    // 
                    isStartPoint = false
                    lastNodeX = startPointX
                    const line = lines.find(line => line.target.id === location.id)
                    overBorderLine.push({'source': line.source.id, 'target': line.target.id})
                }
                locationJson['x'] = lastNodeX
                //  
                locationJson['y'] = lastNodeY - deviationY
                locations.push(locationJson)
                lastPointType = type
            }
        } else if (type === 'parallelgateway' || type === 'branchgateway') {
            //  Y
            resetGatewayList.push(true)
            // 
            lines.find(line => line.target.id === location.id).source.arrow = 'Right'
            lines.find(line => line.target.id === location.id).target.arrow = 'Left'
            if (lastPointType === 'tasknode' || lastPointType === 'subflow') {
                // 
                lastNodeX += shiftX / 2
            }
            lastPointType = type
            // Y
            let nodeY = null
            
            // 
            gatewayFlagList.push(true)
            // 
            let gatewayFlag = null
            if (gatewayFlagList.length - 1 >= 0) {
                // 
                gatewayFlag = gatewayFlagList[gatewayFlagList.length - 1]
            } else {
                gatewayFlag = false
            }
            if (gatewayFlag && gatewayFlagList.length !== 1) {
                //  
                nodeY = Math.max(...gatewayNodeYList)
            } else {
                gatewayNodeYList = [startPointY]
                nodeY = startPointY
            }
            if (newLastNodeX !== null) {
                if (isStartPoint) {
                    // 
                    locationJson['x'] = lastNodeX
                    newLastNodeX = lastNodeX
                    isStartPoint = false
                    const line = lines.find(line => line.target.id === location.id)
                    overBorderLine.push({'source': line.source.id, 'target': line.target.id})
                } else {
                    newLastNodeX += shiftX * 1.5
                    locationJson['x'] = newLastNodeX
                }
                locationJson['y'] = nodeY
                locations.push(locationJson)
                // 
                gatewayNodeYList.push(nodeY)
                //  x,y
                gatewayNodeYList = this.recursionData(gatewayCycleList[nowId], gatewayNodeYList, newLastNodeX, nodeY)
            } else {
                if (isStartPoint) {
                    // 
                    locationJson['x'] = lastNodeX
                    isStartPoint = false
                    const line = lines.find(line => line.target.id === location.id)
                    overBorderLine.push({'source': line.source.id, 'target': line.target.id})
                } else {
                    lastNodeX +=  shiftX / 1.2
                    locationJson['x'] = lastNodeX
                }
                locationJson['y'] = lastNodeY
                locations.push(locationJson)
                // 
                gatewayNodeYList.push(lastNodeY)
                // 
                gatewayNodeYList = this.recursionData(gatewayCycleList[nowId], gatewayNodeYList)
            }
            return [lastNodeX, null, gatewayNodeYList]

        } else if (type === 'convergegateway') {
            resetGatewayList.pop()
            if (resetGatewayList.length === 0) {
                gatewayNodeYList.splice(0, gatewayShiftYList.length)
            }
            // 
            let lastNode = lines.find(line => line.target.id === location.id)
            if (LastLocations.find(location => lastNode.source.id === location.id).type === 'convergegateway'){
                // 
                lastNode.target.arrow = 'Left'
            }
            
            if (newLastNodeX !== null) {
                // 
                newLastNodeX += gatewayShiftX * 1.3
                locationJson['x'] = newLastNodeX
                locationJson['y'] = newLastNodeY
            } else {
                // 
                lastNodeX += gatewayShiftX * 1.3
                locationJson['x'] = lastNodeX
                locationJson['y'] = lastNodeY
            }
            locations.push(locationJson)
            lastPointType = type
            if (newLastNodeX !== null) {
                return [newLastNodeX, newLastNodeY, gatewayNodeYList]
            } else {
                return [lastNodeX, lastNodeY, gatewayNodeYList]
            }
        }
        else if (type === 'endpoint') {
            // 
            let line = lines.find(line => line.target.id === location.id)
            line.target.arrow = 'Left'
            line.source.arrow = 'Right'
            if (isStartPoint) {
                lastNodeX = startPointX
                isStartPoint = false
                const line = lines.find(line => line.target.id === location.id)
                overBorderLine.push({'source': line.source.id, 'target': line.target.id})
            } else if (lastPointType === 'tasknode' || lastPointType === 'subflow') {
                // 1.5
                lastNodeX += shiftX * 1.5
            } else if (lastPointType === 'convergegateway') {
                // 
                lastNodeX += shiftX
            }
            locationJson['x'] = lastNodeX
            locationJson['y'] = lastNodeY
            locations.push(locationJson)
            return
        }
    },
    isOverBorder () {
        if (lastNodeX + shiftX * 1.5 > maxWidth) {
            // 
            lastNodeX = startPointX
            // 
            if (gatewayShiftY === 0) {
                gatewayShiftY = shiftY
            }
            // 
            let newgatewayShiftY = Math.max(...gatewayShiftYList)
            if (gatewayShiftY < newgatewayShiftY) {
                gatewayShiftY = newgatewayShiftY
            }
            // 
            let templLastNodeY = (gatewayShiftY + shiftY) * 1.5
            if (lastNodeY < templLastNodeY) {
                lastNodeY = templLastNodeY
            }
            gatewayShiftY = 0
            // 
            isStartPoint = true
            // X
            lastNodeXList.splice(0, lastNodeXList.length)
            lastNodeXList.push(startPointX)
            convergeGatewayNodeXList.splice(0, lastNodeXList.length)
        }
    },
    // 
    initData () {
        group = {}
        lastPoint = null
        lastNodeX = 60
        lastNodeY = 50
        lastPointType = 'startpoint'
        gatewayShiftY = 0
        gatewayShiftYList.splice(0, gatewayShiftYList.length)
        lastNodeXList.splice(0, lastNodeXList.length)
        convergeGatewayNodeXList.splice(0, convergeGatewayNodeXList.length)
        gatewayFlagList.splice(0, gatewayFlagList.length)
        gatewayStack.splice(0, gatewayStack.length)
        resetGatewayList.splice(0, resetGatewayList.length)
        overBorderLine.splice(0, overBorderLine.length)
        isStartPoint = false
        gatewayCycleList = {}
        newLocations = {}
        gatewayGroup = {}
        gatewayCycleExecute = {}
    },
    // 
    getNextPoint (id) {
        const nextPointId = lines.find(line => line.source.id === id).target.id
        return locations.find(location => location.id === nextPointId)
    },
    // 
    getPreviousPoint (id) {
        const previousPointId = lines.find(line => line.target.id === id).source.id
        return locations.find(location => location.id === previousPointId)
    }
}

export default formatPositionUtils
